	module.exports = {"data":{"package":{"id":"@j3a/bs-xstream","stars":null,"name":"@j3a/bs-xstream","version":"0.1.1","category":"binding","flags":[],"platforms":["any"],"description":"Bucklescript bindings for xstream","keywords":["reactive programming"],"license":"LGPL-3.0","updated":"2018-08-19T14:22:31.690Z","type":"published","score":0.2779902051773651,"quality":0.5274588685580018,"popularity":0.008816794123708279,"maintenance":0.3333333333333333,"readme":"<h1 id=\"bs-xstream\"><a href=\"#bs-xstream\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>bs-xstream</h1>\n<p>Bucklescript/ReasonML bindings for <a href=\"https://github.com/staltz/xstream\">xstream</a>.</p>\n<h2 id=\"coverage\"><a href=\"#coverage\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Coverage</h2>\n<p>The bindings are in alpha.</p>\n<p>They currently cover the following classes:</p>\n<ul>\n<li>Stream</li>\n<li>MemoryStream</li>\n</ul>\n<h2 id=\"how-to-use-it\"><a href=\"#how-to-use-it\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How to use it</h2>\n<p>Xstream relies on fluent interfaces for method chaining. The bindings have written to make the most of bs interop with JS, so you can choose between a fluent or a more functional approach. As per the examples given in <code>examples</code> folder:</p>\n<h3 id=\"functional-style\"><a href=\"#functional-style\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Functional style</h3>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-ocaml\"><code><span class=\"hljs-keyword\">open</span> <span class=\"hljs-type\">Xstream</span>.<span class=\"hljs-type\">Stream</span>;\n\n<span class=\"hljs-keyword\">let</span> producer = fromArray([<span class=\"hljs-number\">1</span>]);\n<span class=\"hljs-keyword\">let</span> transformed = map(producer, x =&gt; x + <span class=\"hljs-number\">5</span>);</code></pre></div>\n<h3 id=\"fluent-style-with-pipes\"><a href=\"#fluent-style-with-pipes\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Fluent style with pipes</h3>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-ocaml\"><code><span class=\"hljs-keyword\">open</span> <span class=\"hljs-type\">Xstream</span>.<span class=\"hljs-type\">Stream</span>;\n\n<span class=\"hljs-keyword\">let</span> producer = fromArray([<span class=\"hljs-number\">1</span>]);\n<span class=\"hljs-keyword\">let</span> piped =\n  producer\n  -&gt;map(x =&gt; x + <span class=\"hljs-number\">5</span>)\n  -&gt;filter(x =&gt; x &gt; <span class=\"hljs-number\">5</span>)\n  -&gt;remember<span class=\"hljs-literal\">()</span>;</code></pre></div>\n<h1 id=\"build\"><a href=\"#build\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Build</h1>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-none\"><code>npm run build</code></pre></div>\n<h1 id=\"build--watch\"><a href=\"#build--watch\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Build + Watch</h1>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-none\"><code>npm run start</code></pre></div>\n<h1 id=\"editor\"><a href=\"#editor\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Editor</h1>\n<p>If you use <code>vscode</code>, Press <code>Windows + Shift + B</code> it will build automatically</p>\n","homepageUrl":"http://staltz.github.io/xstream/","repositoryUrl":"https://gitlab.com/j3a-solutions/bs-xstream","npmUrl":"https://www.npmjs.com/package/%40j3a%2Fbs-xstream","issuesUrl":"https://gitlab.com/j3a-solutions/bs-xstream/issues","slug":"/package/@j3a/bs-xstream"}},"pathContext":{"id":"@j3a/bs-xstream"}}

